*, *::after, *::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  min-height: 100vh;
  position: relative;
  display: grid;
  place-items: center;
}

main {
  min-height: 100vh;
  text-align: center;
}
main .hero-sec {
  border-bottom-left-radius: 1.7rem;
  border-bottom-right-radius: 1.7rem;
}
main .summary-sec h3 {
  margin-bottom: 1rem;
}
main button {
  width: 100%;
  font-family: inherit;
  padding: 0.8rem;
  border-radius: 2rem;
  border: none;
}

footer {
  position: absolute;
  bottom: 0;
}

@media only screen and (min-width: 700px) {
  main {
    display: grid;
    grid-template-columns: repeat(2, minmax(auto, 19.4444444444rem));
    max-width: 38.8888888889rem;
    min-height: 23.6111111111rem;
    width: 52%;
    overflow: hidden;
    border-radius: 1.7rem;
    box-shadow: 5px 4px 15px 2px rgba(0, 0, 0, 0.2);
  }
  main .hero-sec {
    border-top-right-radius: 1.7rem;
    padding: 3.2rem;
    width: 95%;
  }
  main .hero-sec h1 {
    margin-bottom: 2rem;
    margin-top: 0.7rem;
  }
  main .hero-sec h2 {
    margin-bottom: 1.7rem;
  }
  main .summary-sec h3 {
    margin-top: 1rem;
    margin-bottom: 1.5rem;
  }
}